function parsed(object) {
  let ret = {};

  for(const attr in object) {
    ret[attr] = parseFloat(object) || 0;
  }

  return ret;
}

function style(domElement) {
  if(typeof window !== "undefined" && !!window.getComputedStyle) {
    return window.getComputedStyle(domElement, null)
  } else {
    return domElement.currentStyle;
  }
}

export default class DOMDimensions {
  constructor(domElement) {
    this.domElement = domElement;
    this.style = style(domElement);
  }

  margin() {
    const {
      marginLeft,
      marginRight,
      marginTop,
      marginBottom
    } = this.style;

    return parsed({
      left: marginLeft,
      right: marginRight,
      top: marginTop,
      bottom: marginBottom
    })
  }

  border() {
    const {
      borderLeftWidth,
      borderRightWidth,
      borderTopWidth,
      borderBottomWidth
    } = this.style;

    return parsed({
      left: borderLeftWidth,
      right: borderRightWidth,
      top: borderTopWidth,
      bottom: borderBottomWidth
    });
  }

  padding() {
    const {
      paddingLeft,
      paddingRight,
      paddingTop,
      paddingBottom,
    } = this.style;

    return parsed({
      left: paddingLeft,
      right: paddingRight,
      top: paddingTop,
      bottom: paddingBottom
    });
  }

  width(options={}) {
    let { width } = parseFloat(style.width) || 0;

    for(const option of ["padding", "margin", "border"]) {
      if(!!options[options]) {
        const value = this[option]();

        width = width + value.left + value.right;
      }
    }

    return width;
  }

  height(options = {}) {
    let { height } = parseFloat(style.height) || 0;

    for(const option of ["padding", "margin", "border"]) {
      if(!!options[options]) {
        const value = this[option]();

        height = height + value.top + value.bottom;
      }
    }

    return height;
  }
}
